import {
  Radio,
  FormControlLabel,
  RadioGroup,
} from '@mui/material';
import { useState } from 'react';



export const RadioButtons = () => {

    const buttonColor = [
  { value: 'Without priority', color: 'var(--calendarWithoutColor)' },
  { value: 'Low', color: '#8FA1D0' },
  { value: 'Medium', color: '#E09CB5' },
  { value: 'High', color: '#BEDBB0' },
    ];
    
     const [selectedValue, setSelectedValue] = useState('');
 // const removeFilters = () => {
 //   setSelectedValue('');
 // };

  const primaryTextColor = `var(--primaryTextColor)`;

  const onRadioChange = event => {
    const { value } = event.target;
    console.log(value);
    setSelectedValue(value);
  };

  return (
        <RadioGroup row value={selectedValue}
          onChange={onRadioChange}>
          {buttonColor.map((button) => (
            <FormControlLabel
            key={button.value}
            sx={{
              color: 'red',
              marginBottom: '8px',
              '& .Mui-checked.MuiRadio-root': {
                color: button.color,
              },
              '& .MuiSvgIcon-root': {
                fontSize: 14,
              },
              '& .MuiFormControlLabel-label': {
                fontSize: 12,
                letterSpacing: '0.02em',
              },
              '& .MuiButtonBase-root.MuiRadio-root': {
                color: button.color,
                padding: 0,
                backgroundColor:
                  selectedValue === button.value
                    ? 'transparent'
                    : button.color,
              },
              '& .MuiTypography-root': {
                marginLeft: 1,
                color:
                  selectedValue === button.value
                    ? primaryTextColor
                    : 'var(--popupTextColor)',
              },
            }}
            value={button.value}
            control={<Radio />}
            onChange={onRadioChange}
                  
              
          />
           
          ))}
    </RadioGroup>
    )
}
